@import './vars';

%extends-absolut-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

%extends-flex-y-center {
  display: flex;
  align-items: center;
}

%extends-flex-center {
  @extend %extends-flex-y-center;
  justify-content: center;
}

%extends-flex-x-between-y-center {
  @extend %extends-flex-y-center;
  justify-content: space-between;
}

%extends-inline-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

%extends-hide-scrollbar {
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
    width: 0;
  }
}

%extends-text-selection {
  &::selection {
    background-color: $selection;
    color: $black;
  }

  &::-moz-selection {
    background-color: $selection;
    color: $black;
  }
}

%extends-disable-blinking-link {
  backface-visibility: hidden; // Standard property
  -webkit-backface-visibility: hidden; // Chrome, Safari
  -webkit-tap-highlight-color: transparent; // Chrome, Safari
}
